import React from "react";
import { useTheme } from "@mui/material/styles";

export default function AboutMe() {
  const theme = useTheme();
  const isDarkMode = theme.palette.mode === "dark";

  return (
    <div className="aos-init aos-animate" data-aos="fade-up">
      <div className="icon mb-3">
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g fill="none" fillRule="evenodd">
            <path d="M0 0h24v24H0z"></path>
            <path
              d="M7 3h10a4 4 0 110 8H7a4 4 0 110-8zm0 6a2 2 0 100-4 2 2 0 000 4z"
              fill="#335EEA"
            ></path>
            <path
              d="M7 13h10a4 4 0 110 8H7a4 4 0 110-8zm10 6a2 2 0 100-4 2 2 0 000 4z"
              fill="#335EEA"
              opacity=".3"
            ></path>
          </g>
        </svg>
      </div>

      <h5 style={{ color: isDarkMode ? "#FFFFFF" : "#000000" }}>Who I Am</h5>

      <p
        className="mb-6 mb-md-0 mt-3"
        style={{
          color: isDarkMode ? "#FFFFFF" : "#555555",
        }}
      >
        I am a developer focused on building scalable applications. I constantly
        explore new tech to enhance my problem-solving skills, and this project
        showcases my ability to combine design and...
      </p>
    </div>
  );
}
